body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;

}

.border {
  padding:5px;
  border: 1px solid black;
  width:40px;
  display:inline-block;
}
/* REUSE CODE */
.selectorCircle { 
  height:50px;
  width:50px;
  border-radius: 50%;
  display: inline-block;
  margin:5px;
  border:1px solid black;
}

.selectedCircle {
  height:40px;
  width:40px;
  border-radius: 50%;
  display: inline-block;
  margin:5px;
  border:1px solid black;
}

.markerCircle {
  height:10px;
  width:10px;
  border-radius: 50%;
  display: inline-block;
  margin:5px;
  border:1px solid black;
}

.serialNo {
  padding:5px;
  /* border:1px solid black; */
  width:20px;
  height: 20px;
  display: inline-block;
  position: relative;
  top:6px;
  margin-right: 10px; 
}

.markerDiv {
  width:50px;
  /* display: inline-block; */
}

.selected {
  display: inline-block;
  margin-right:40px;
}

.marker {
  display: inline-block;
  position: relative;
  top:30px;
}

.selector {
  margin-top: 20px;
  text-align: center;
}

.listSelected {
  height: 400px;
  overflow-y: scroll;
  text-align: center;
}

@media screen and (max-height:600px){
  .listSelected {
    height:200px;
  }
}

.button {
  background-color: khaki;
  border:  1px solid black;
  padding:10px 20px;
  box-shadow: 0 0 5px #999;
  font-family: 'Staatliches', cursive;
  font-size: 18px;

}
.button:hover {
  box-shadow: 0 0 10px #555;
  background-color: burlywood;
  /* transition: border 1s; */
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}

.button_sep {
  margin:10px;
}

.button_container {
  margin-top:10px;
  text-align: center;

}

.result {
  text-align: center;
  font-family: 'Major Mono Display', monospace;
  font-size: 28px;
  line-height: 30px;
  font-weight: bold;
}

.winner {
  text-align: center;
  font-family: 'Staatliches', cursive;
}

.reset_button {
  text-align: center;
  margin-top: 20px;
}

.reset_button button {
  background-color: firebrick;
  border:  1px solid black;
  padding:10px 20px;
  box-shadow: 0 0 5px #999;
  color: cornsilk;
  position: relative;
  left: 8px;
  font-family: 'Staatliches', cursive;
  font-size: 18px;

}

.highlight {
  border: 1px solid #000;
}
